@import url("https://fonts.googleapis.com/css2?family=DotGothic16&family=Inter:wght@200&display=swap");

@media (min-width: 320px) and (max-width: 480px) {
.BurgerMenu-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 350px;
  background-color: black;
  display: flex;
  // align-items: center;
  justify-content: center;
  z-index: 999999;
  .BurgerMenu-content {
    width: 100%;

    ul {
      flex-direction: column;
    list-style: none;
    min-height: 400px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
      li {
        color: red;
      }

      .link-dropdown-header-menu-list {
        text-decoration: none;

        font-family: "Inter", sans-serif;
        font-size: 25px;
        letter-spacing: 0.2px;
        text-align: center;
        color: #FFFFFF;
      }
    }
  }
}
}
